<template>
  <div>
    <!--  -->
    <div
      class="reader-container"
      :style="{
        width: pageWidth + 20 + 'px',
        backgroundColor: colorSettings.borderColor,
        '--page-text-color': colorSettings.textColor,
        '--page-bg-color': colorSettings.bgColor,
        '--page-border-color': colorSettings.borderColor,
      }"
      @mousewheel="wheelHandle"
    >
      <!-- //////////  ////////// -->
      <div class="reader-head">
        <span class="header-text">{{ getTitle() }}</span>
      </div>

      <!-- //////////  ////////// -->
      <transition name="catalogFade">
        <!--  -->
        <div
          v-show="catalogVisible"
          class="catalog-container"
          :style="{
            'user-select': 'none',
            height: pageHeight + 'px',
            width: getCatalogWidth() + 'px',
          }"
        >
          <!--  -->
          <div style="margin: 10px 0 0 10px; height: 40px">
            <!--  -->
            <el-icon class="catalog-button" :size="24" @click="closeCatalog"><Back /></el-icon>
            <!--  -->
            <span class="catalog-title" :style="'width:' + (getCatalogWidth() - 70) + 'px;'">
              {{ bookName }}
            </span>
            <!--  -->
            <!-- <el-icon :size="26"><Notebook /></el-icon> -->
          </div>
          <!-- Tabs -->
          <el-tabs class="catalog-tabs" tab-position="bottom" :stretch="true">
            <!-- tab-pane  -->
            <el-tab-pane>
              <template #label>
                <span class="custom-tabs-label">
                  <el-icon :style="'top:2px;font-size:16px'"><calendar /></el-icon>
                  <span :style="'margin-left:4px;font-size:18px'"></span>
                </span>
              </template>
              <!--  -->
              <div ref="catalogPane" :style="'overflow: auto; height:' + (pageHeight - 120) + 'px'">
                <div :style="{ height: catalogBeforeHeight + 'px' }"></div>
                <div
                  v-for="(item, index) in chapterListRendered"
                  :key="index"
                  class="catalog-item"
                  :style="
                    'font-weight:' +
                    (item.id == currentChapter ? 'bold' : 'normal') +
                    ';color:' +
                    (item.id == currentChapter ? '#990066' : '#252525')
                  "
                  @click="selectChapter(item.id)"
                >
                  {{ item.title }}
                </div>
                <div :style="{ height: catalogAfterHeight + 'px' }"></div>
              </div>
              <!-- {{ ("000" + item.id).substr(-4) }}&nbsp;-&nbsp;{{ item.title }} -->
            </el-tab-pane>
            <!-- tab-pane  -->
            <el-tab-pane>
              <template #label>
                <span class="custom-tabs-label">
                  <el-icon :style="'top:2px;font-size:16px'"><CollectionTag /></el-icon>
                  <span :style="'margin-left:4px;font-size:18px'"></span>
                </span>
              </template>
              <!--  -->
              <div :style="'overflow: auto; height:' + (pageHeight - 120) + 'px'">
                <div
                  v-if="!bookmarks.length"
                  style="color: #777777; text-align: center; margin-top: 20px"
                >
                  
                </div>
                <div
                  v-for="(item, index) in bookmarks"
                  :key="index"
                  class="bookmark-item"
                  @click="jumpToBookmark(index)"
                >
                  <div class="bookmark-item-title">
                    <span :style="'width:' + (getCatalogWidth() - 70) + 'px;'">{{
                      chapterList[item.chapter].title
                    }}</span>
                    <el-icon
                      class="link-type"
                      style="float: right; margin-right: 20px"
                      :size="18"
                      @click.stop="deleteBookmark(index)"
                      ><Remove
                    /></el-icon>
                  </div>
                  <div class="bookmark-item-small-text">
                    <!-- <el-icon :size="14" style="top: 2px"><Timer /></el-icon> -->
                    <span>{{ item.time }}&emsp;</span>
                    <!-- <svg-icon icon-class="list" style="font-size: 10px" />&nbsp; -->
                    <span>{{ item.chapter + " / " + chapterList.length }}</span>
                  </div>
                  <div class="bookmark-item-preview">{{ getBookmarkPreview(index) }}</div>
                </div>
              </div>
            </el-tab-pane>
            <!-- tab-pane  -->
            <el-tab-pane>
              <template #label>
                <span class="custom-tabs-label">
                  <el-icon :style="'top:2px;font-size:16px'"><Edit /></el-icon>
                  <span :style="'margin-left:4px;font-size:18px'"></span>
                </span>
              </template>
              <div :style="'overflow: auto; height:' + (pageHeight - 120) + 'px'">
                <div
                  v-if="!notes.length"
                  style="color: #777777; text-align: center; margin-top: 20px"
                >
                  
                </div>
                <div
                  v-for="(item, index) in notes"
                  :key="index"
                  class="bookmark-item"
                  @click="jumpToNote(index)"
                >
                  <div class="bookmark-item-title">
                    <span :style="'width:' + (getCatalogWidth() - 70) + 'px;'">{{
                      "" + (item.remark == "" ? "()" : item.remark)
                    }}</span>
                    <el-icon
                      class="link-type"
                      style="float: right; margin-right: 20px"
                      :size="18"
                      @click.stop="deleteNote(index)"
                      ><Remove
                    /></el-icon>
                  </div>
                  <div class="bookmark-item-small-text">
                    <span>{{ item.time }}&emsp;</span>
                    <span>{{ limitText(chapterList[item.chapter].title, 22) }}</span>
                  </div>
                  <div class="bookmark-item-preview">
                    <span :style="'background-color:' + item.color">{{
                      getNotePreview(index)
                    }}</span>
                  </div>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </transition>

      <!-- //////////  ////////// -->
      <div
        class="body-button-container"
        :style="'width: ' + pageWidth + 'px;visibility:' + (catalogVisible ? 'hidden' : 'inherit')"
      >
        <!--  -->
        <span class="body-button goback" @click="handleGoBack">
          <el-icon class="body-button-icon" :size="32"><Back /></el-icon>
        </span>
        <!--  -->
        <span
          class="body-button catalog"
          :style="'bottom: ' + -pageHeight + 'px'"
          @click="openCatalog"
        >
          <el-icon class="body-button-icon" :size="32"><List /></el-icon>
        </span>
        <!--  -->
        <span
          class="body-button prevchapter"
          :style="'left: ' + pageWidth / 3 + 'px'"
          @click="handleDebounce(prevChapter)"
        >
          <el-icon class="body-button-icon" :size="32"><CaretLeft /></el-icon>
        </span>
        <!--  -->
        <span
          class="body-button nextchapter"
          :style="'left: ' + (pageWidth * 2) / 3 + 'px'"
          @click="handleDebounce(nextChapter)"
        >
          <el-icon class="body-button-icon" :size="32"><CaretRight /></el-icon>
        </span>
        <!--  -->
        <span
          class="body-button prevpage"
          :style="'top: ' + pageHeight / 2 + 'px'"
          @click="handleDebounce(prevPage)"
          @dblclick="handleDebounce(firstPage, 50)"
        >
          <el-icon class="body-button-icon" :size="32"><ArrowLeftBold /></el-icon>
        </span>
        <!--  -->
        <span
          class="body-button nextpage"
          :style="'top: ' + pageHeight / 2 + 'px'"
          @click="handleDebounce(nextPage)"
          @dblclick="handleDebounce(lastPage, 50)"
        >
          <el-icon class="body-button-icon" :size="32"><ArrowRightBold /></el-icon>
        </span>
        <!--   -->
        <el-popover
          :visible="voiceVisible"
          placement="top-end"
          :show-arrow="false"
          :width="popoverWidth"
        >
          <div style="margin: 10px 10px; user-select: none">
            <div>
              <span style="margin-right: 15px"></span>
              <el-select
                v-model="voiceIndex"
                placeholder=""
                no-match-text=""
                style="width: 252px"
                @change="changeVoice()"
              >
                <el-option
                  v-for="(item, index) in voiceList"
                  :key="index"
                  :label="voiceNameFormat(item)"
                  :value="index"
                >
                  <span
                    style="
                      width: 280px;
                      float: right;
                      color: var(--el-text-color-secondary);
                      font-size: 14px;
                    "
                    >{{ index + 1 + " - " + voiceNameFormat(item) }}</span
                  >
                </el-option>
              </el-select>
            </div>
            <div style="margin-top: 20px">
              <span></span>
              <el-slider
                v-model="speechOptions.rate"
                :min="0.1"
                :max="3"
                :step="0.1"
                show-input
                class="setting-slider"
              />
            </div>
            <div>
              <span></span>
              <el-slider
                v-model="speechOptions.volume"
                :min="0"
                :max="1"
                :step="0.05"
                show-input
                class="setting-slider"
              />
            </div>
            <div>
              <span></span>
              <el-slider
                v-model="speechOptions.pitch"
                :min="0"
                :max="2"
                :step="0.1"
                show-input
                class="setting-slider"
              />
            </div>
            <el-divider border-style="dashed" style="margin: 10px 0"></el-divider>
            <div>
              <span>{{ " ( " + speechTimeLeft + "  )" }} </span>
              <el-slider
                v-model="speechTimeLeft"
                show-stops
                :min="0"
                :max="120"
                :step="10"
                class="setting-slider"
              />
              <span style="margin-right: 20px"></span>
              <el-switch :disabled="!isSpeaking" v-model="timedSpeechOpen" @change="timedSpeech" />
            </div>
            <el-divider border-style="dashed" style="margin: 5px 0"></el-divider>
            <div style="margin-top: 20px">
              <!-- <span style="margin-right: 10px"></span> -->
              <el-button type="primary" plain round @click="speechStart(0)"></el-button>
              <el-button type="primary" plain round @click="speechResume"></el-button>
              <el-button type="primary" plain round @click="speechPause"></el-button>
              <el-button type="primary" plain round @click="speechStop"></el-button>
            </div>
          </div>
          <template #reference>
            <span
              class="body-button voice"
              :style="'bottom: ' + -pageHeight + 'px'"
              @click="openVoice"
            >
              <el-icon class="body-button-icon" :size="32"><VideoPlay /></el-icon>
            </span>
          </template>
        </el-popover>
        <!--   -->
        <el-popover
          :visible="settingsVisible"
          popper-class="popover-settings"
          placement="bottom-end"
          :width="popoverWidth"
        >
          <!--  -->
          <el-tabs type="border-card" style="min-height: 510px; user-select: none">
            <!--  -->
            <el-tab-pane label="">
              <span></span>
              <el-slider
                v-model="sizeSettings.fontSize"
                :min="12"
                :max="48"
                :step="1"
                show-input
                class="setting-slider"
              />
              <span></span>
              <el-slider
                v-model="sizeSettings.lineHeight"
                :min="1"
                :max="2"
                :step="0.1"
                show-input
                class="setting-slider"
              />
              <span></span>
              <el-slider
                v-model="sizeSettings.paraSpace"
                :min="0"
                :max="1"
                :step="0.1"
                show-input
                class="setting-slider"
              />
              <span></span>
              <el-slider
                v-model="sizeSettings.titleScale"
                :min="1"
                :max="3"
                :step="0.1"
                show-input
                class="setting-slider"
              />
              <el-divider border-style="dashed" style="margin: 10px 0" />
              <span></span>
              <el-slider
                v-model="sizeSettings.paddingTop"
                :min="0"
                :max="40"
                :step="2"
                show-input
                class="setting-slider"
              />
              <span></span>
              <el-slider
                v-model="sizeSettings.paddingBottom"
                :min="0"
                :max="40"
                :step="2"
                show-input
                class="setting-slider"
              />
              <span></span>
              <el-slider
                v-model="sizeSettings.paddingLR"
                :min="0"
                :max="40"
                :step="2"
                show-input
                class="setting-slider"
              />
              <span></span>
              <el-slider
                v-model="sizeSettings.widthPercent"
                :min="20"
                :max="80"
                :step="5"
                show-input
                class="setting-slider"
              />
            </el-tab-pane>
            <!--  -->
            <el-tab-pane label="">
              <div>
                <div style="margin-bottom: 10px"></div>
                <div
                  style="margin: 2px 2px; display: inline-block"
                  v-for="(item, index) in fonts"
                  :key="index"
                >
                  <el-button
                    :type="sizeSettings.fontFamily == item ? 'primary' : 'default'"
                    @click="sizeSettings.fontFamily = item"
                    >{{ item }}</el-button
                  >
                </div>
                <div style="margin-top: 15px"></div>
                <div
                  :style="{
                    'font-family': sizeSettings.fontFamily,
                    margin: '5px 0',
                    'text-align': 'center',
                  }"
                >
                  <div style="font-size: 30px">Hello </div>
                  <div style="font-size: 24px">Hello </div>
                  <div style="font-size: 18px">Hello </div>
                </div>
              </div>
            </el-tab-pane>
            <!--  -->
            <el-tab-pane label="">
              <div class="theme-setbar">
                <span style="margin-right: 10px"></span>
                <div style="float: right">
                  <template
                    v-for="(i, index) in [
                      [0, 0],
                      [0, 1],
                      [0, 2],
                      [5, 4],
                      [3, 8],
                      [0, 5],
                      [2, 9],
                      [7, 8],
                    ]"
                    :key="index"
                  >
                    <span
                      class="text-button"
                      :style="'color:' + textColors[i[0]] + ';background-color:' + bgColors[i[1]]"
                      @click="
                        ;(colorSettings.textColor = textColors[i[0]]),
                          (colorSettings.bgColor = bgColors[i[1]])
                      "
                      >{{ index + 1 }}</span
                    >
                    <br v-if="index == 3" /><br v-if="index == 3" />
                  </template>
                </div>
              </div>
              <el-divider border-style="dashed"></el-divider>
              <div>
                <span style="margin-right: 10px"></span>
                <div style="float: right">
                  <span
                    v-for="(item, index) in [
                      require('./images/paper01.jpg'),
                      require('./images/paper02.png'),
                      require('./images/paper03.png'),
                      require('./images/paper04.png'),
                      require('./images/paper05.png'),
                    ]"
                    :key="index"
                    class="text-button"
                    :style="{
                      'margin-right': '5px',
                      background: 'url(' + item + ')',
                    }"
                    @click="colorSettings.bgColor = 'url(' + item + ')'"
                    >&emsp;&ensp;</span
                  >
                </div>
              </div>
              <el-divider border-style="dashed"></el-divider>
              <div class="color-setbar" style="clear: right">
                <span></span
                ><el-color-picker
                  v-model="colorSettings.textColor"
                  show-alpha
                  :predefine="predefineColors"
                />
                <div style="float: right; width: 180px; line-height: 30px">
                  <div>
                    <el-button
                      v-for="(item, index) in textColors.slice(0, 5)"
                      :key="index"
                      size="small"
                      :color="item"
                      @click="colorSettings.textColor = item"
                      class="color-button"
                    ></el-button>
                  </div>
                  <div>
                    <el-button
                      v-for="(item, index) in textColors.slice(5, 10)"
                      :key="index"
                      size="small"
                      :color="item"
                      @click="colorSettings.textColor = item"
                      class="color-button"
                    ></el-button>
                  </div>
                </div>
              </div>
              <div class="color-setbar" style="clear: right">
                <span></span
                ><el-color-picker
                  v-model="colorSettings.bgColor"
                  show-alpha
                  :predefine="predefineColors"
                />
                <div style="float: right; width: 180px; line-height: 30px">
                  <div>
                    <el-button
                      v-for="(item, index) in bgColors.slice(0, 5)"
                      :key="index"
                      size="small"
                      :color="item"
                      @click="colorSettings.bgColor = item"
                      class="color-button"
                    ></el-button>
                  </div>
                  <div>
                    <el-button
                      v-for="(item, index) in bgColors.slice(5, 10)"
                      :key="index"
                      size="small"
                      :color="item"
                      @click="colorSettings.bgColor = item"
                      class="color-button"
                    ></el-button>
                  </div>
                </div>
              </div>
              <div class="color-setbar" style="clear: right">
                <span></span
                ><el-color-picker
                  v-model="colorSettings.borderColor"
                  show-alpha
                  :predefine="predefineColors"
                />
                <div style="float: right; width: 180px; line-height: 30px">
                  <div>
                    <el-button
                      v-for="(item, index) in bgColors.slice(0, 5)"
                      :key="index"
                      size="small"
                      :color="item"
                      @click="colorSettings.borderColor = item"
                      class="color-button"
                    ></el-button>
                  </div>
                  <div>
                    <el-button
                      v-for="(item, index) in bgColors.slice(5, 10)"
                      :key="index"
                      size="small"
                      :color="item"
                      @click="colorSettings.borderColor = item"
                      class="color-button"
                    ></el-button>
                  </div>
                </div>
              </div>
            </el-tab-pane>
            <!--  -->
            <el-tab-pane label="">
              <div style="margin-bottom: 10px">
                <div style="margin-bottom: 10px"></div>
                <el-radio-group v-model="pageTurning">
                  <el-radio-button
                    v-for="(item, index) in pageTurningModes"
                    :key="index"
                    :label="item"
                  />
                </el-radio-group>
                <div style="margin-top: 20px; margin-bottom: 10px"></div>
                <el-button @click="resetStyle"></el-button>
              </div>
            </el-tab-pane>
          </el-tabs>
          <template #reference>
            <span class="body-button settings" @click="openSettings">
              <el-icon class="body-button-icon" :size="32"><Tools /></el-icon>
            </span>
          </template>
        </el-popover>
        <!--   -->
        <el-popover
          :visible="searchVisible"
          popper-class="popover-search"
          placement="bottom"
          :show-arrow="false"
          :width="popoverWidth"
          :offset="-30"
          @hide="tempParaIndex = null"
        >
          <div style="margin: 10px 10px; user-select: none">
            <div style="display: flex; justify-content: center">
              <el-button
                style="margin: 0 10px; align-self: center"
                size="small"
                icon="ArrowLeftBold"
                round
                @click="handleSearch(keywords, true)"
              ></el-button>
              <!-- <div style="display: inline-block; text-align: center; width: 180px"> -->
              <el-input
                v-model="keywords"
                placeholder=""
                prefix-icon="Search"
                clearable
                @keyup.enter="handleSearch(keywords)"
              ></el-input>
              <!-- </div> -->
              <el-button
                style="margin: 0 10px; align-self: center"
                size="small"
                icon="ArrowRightBold"
                round
                @click="handleSearch(keywords)"
              ></el-button>
            </div>
          </div>
          <template #reference>
            <span
              class="body-button search"
              :style="'left: ' + pageWidth / 2 + 'px'"
              @click="openSearch"
            >
              <el-icon class="body-button-icon" :size="30"><Search /></el-icon>
            </span>
          </template>
        </el-popover>
        <!--   -->
        <el-popover
          :visible="progressVisible"
          placement="top"
          :show-arrow="false"
          :width="popoverWidth"
          :offset="-30"
        >
          <div style="margin: 5px 10px; user-select: none">
            <div style="text-align: center">
              <span style="color: var(--el-text-color-secondary)">{{ progress }}%</span>
            </div>
            <div style="margin: 0">
              <el-button
                style="margin-right: 20px"
                size="small"
                icon="ArrowLeftBold"
                round
                @click="handleDebounce(prevChapter)"
              ></el-button>
              <div style="display: inline-block; text-align: center; width: 180px">
                <el-slider
                  v-model="progress"
                  :min="0"
                  :max="100"
                  :step="0.1"
                  :show-tooltip="false"
                  @click="jumpTo(progress)"
                  @change="jumpTo"
                />
              </div>
              <el-button
                style="margin-left: 20px"
                size="small"
                icon="ArrowRightBold"
                round
                @click="handleDebounce(nextChapter)"
              ></el-button>
            </div>
            <div style="text-align: center; margin-top: 5px">
              <span class="link-type" style="font-size: 16px" @click="addBookmark">
                <el-icon :size="18" style="top: 4px"><CirclePlus /></el-icon>
                
              </span>
            </div>
          </div>
          <template #reference>
            <span
              class="body-button progress"
              :style="'left: ' + pageWidth / 2 + 'px;bottom: ' + -pageHeight + 'px'"
              @click="openProgress"
            >
              <el-icon class="body-button-icon" :size="32"><Odometer /></el-icon>
            </span>
          </template>
        </el-popover>
        <!-- Home -->
        <span
          class="body-button home"
          :style="'left: ' + pageWidth / 2 + 'px;bottom: ' + -pageHeight + 'px;visibility:visible'"
          @click="closePopover"
        >
          <!-- fullscreen('reader-container') -->
        </span>
        <!--  -->
        <el-popover
          :visible="menuVisible"
          popper-class="popover-menu"
          :show-arrow="false"
          :placement="menuPlacement"
          :width="195"
          :offset="0"
        >
          <div style="user-select: none">
            <div class="popmenu-title">
              <span>{{ limitText(getCurrentParaText(), 8) }}</span>
              <el-icon
                class="link-type"
                style="float: right; margin-bottom: 10px"
                :size="18"
                @click="menuVisible = false"
                ><CloseBold
              /></el-icon>
            </div>
            <div style="clear: both">
              <el-button
                class="menu-button-short"
                size="small"
                type="primary"
                plain
                @click="speechStart(currentPara), closePopover()"
                ></el-button
              >
              <el-button
                class="menu-button-short"
                style="margin-left: 9px"
                size="small"
                type="primary"
                plain
                @click="speechResume(), closePopover()"
                ></el-button
              >
              <el-button
                class="menu-button-short"
                style="margin-left: 0"
                size="small"
                type="primary"
                plain
                @click="speechPause(), closePopover()"
                ></el-button
              >
              <el-button
                class="menu-button-short"
                style="margin-left: 9px"
                size="small"
                type="primary"
                plain
                @click="speechStop(), closePopover()"
                ></el-button
              >
            </div>
            <el-divider border-style="dashed" style="margin: 5px 0"></el-divider>
            <el-input
              v-model="noteRemark"
              placeholder=""
              prefix-icon="Edit"
              clearable
              type="textarea"
              style="margin: 5px 0"
              resize="none"
              :autosize="{ minRows: 1, maxRows: 3 }"
              :maxlength="500"
              @keyup.enter="addNote(currentPara, noteRemark, noteColor, true)"
            ></el-input>
            <el-button
              class="menu-button"
              size="small"
              type="primary"
              icon="Plus"
              plain
              @click="addNote(currentPara, noteRemark, noteColor, true)"
              >{{ searchMarkedPara(currentPara) > -1 ? "" : "" }}</el-button
            >
            <el-button
              v-show="searchMarkedPara(currentPara) > -1"
              class="menu-button"
              style="margin-left: 0"
              size="small"
              type="primary"
              icon="Minus"
              plain
              @click="handleDeleteNote(currentPara)"
              ></el-button
            >
            <div style="line-height: 30px">
              <div>
                <el-button
                  v-for="(item, index) in markedColors.slice(0, 5)"
                  :key="index"
                  size="small"
                  :color="item"
                  @click="noteColor = item"
                  :style="{
                    border: noteColor == item ? '1px dashed #222' : '1px solid #FFF',
                  }"
                ></el-button>
              </div>
              <div>
                <el-button
                  v-for="(item, index) in markedColors.slice(5, 10)"
                  :key="index"
                  size="small"
                  :color="item"
                  @click="noteColor = item"
                  :style="{
                    border: noteColor == item ? '1px dashed #222' : '1px solid #FFF',
                  }"
                ></el-button>
              </div>
            </div>
          </div>
          <template #reference>
            <span
              :style="{ position: 'absolute', left: menuOffsetX + 'px', top: menuOffsetY + 'px' }"
            ></span>
          </template>
        </el-popover>
      </div>

      <!-- //////////  ////////// -->
      <div
        class="reader-body"
        ref="readerBody"
        :style="{
          'font-size': sizeSettings.fontSize + 'px',
          'font-family': sizeSettings.fontFamily,
          'line-height': sizeSettings.lineHeight,
          'padding-top': sizeSettings.paddingTop + 'px',
          'padding-bottom': sizeSettings.paddingBottom + 'px',
          'padding-left': sizeSettings.paddingLR + 'px',
          'padding-right': sizeSettings.paddingLR + 'px',
          height: pageHeight + 'px',
          color: colorSettings.textColor,
          background: colorSettings.bgColor,
          'background-position': Math.floor((currentPage / (pageList.length ?? 1)) * 100) + '%',
          overflow: pageTurning == '' ? 'auto' : 'visible',
        }"
        @click="closePopover"
        @mousedown="closePopupMenu"
        @mouseup.prevent="handleMouseup"
      >
        <!--  -->
        <p
          v-if="hasNoIndentPara()"
          class="main-para"
          :style="{
            margin: sizeSettings.paraSpace * sizeSettings.fontSize + 'px 0',
            'text-indent': 0,
            color: getParaTextColor(0),
            'background-color': getParaBgColor(0),
            'text-shadow': getTextShadow(0),
          }"
          @click="speechFromPara(0)"
          @mousedown="currentPara = 0"
        >
          {{ noIndentPara }}
        </p>
        <!--  -->
        <p
          v-for="(item, index) in indentParas"
          :key="index"
          class="main-para"
          :style="{
            margin: sizeSettings.paraSpace * sizeSettings.fontSize + 'px 0',
            'font-size': getParaFontSize(index) + 'px',
            'font-weight': index == 0 && currentPage == 0 ? 'bold' : 'normal',
            color: hasNoIndentPara() ? getParaTextColor(index + 1) : getParaTextColor(index),
            'background-color': hasNoIndentPara()
              ? getParaBgColor(index + 1)
              : getParaBgColor(index),
            'text-shadow': hasNoIndentPara() ? getTextShadow(index + 1) : getTextShadow(index),
          }"
          @click="speechFromPara(noIndentPara == '' ? index : index + 1)"
          @mousedown="currentPara = noIndentPara == '' ? index : index + 1"
        >
          {{ item }}
        </p>
      </div>

      <!-- //////////  ////////// -->
      <div class="reader-foot">
        <span class="footer-text" style="float: left; text-align: left; margin-left: 10px"
          >{{
            device == "desktop"
              ? currentChapter + 1 + " / " + (chapterList.length ?? 0) + " &emsp;"
              : ""
          }}{{ currentPage + 1 + " / " + (pageList.length ?? 0) + " " }}</span
        >
        <span
          v-if="device == 'desktop'"
          class="footer-text"
          style="float: right; text-align: right; margin-right: 10px"
          >{{ getPageWordNum() }}/{{ getTotalWordNum() }}</span
        >
      </div>
    </div>
  </div>
</template>

<script>
import { useStore } from "vuex"
import ConstantsMixin from "./mixins/SettingConstants"
import RepageMixin from "./mixins/RepageHandler"
import SpeechMixin from "./mixins/SpeechHandler"

export default {
  name: "YunReader",
  props: ["changeMode", "inReader", "bookId", "bookName", "articleData", "chapterList"],
  mixins: [ConstantsMixin, RepageMixin, SpeechMixin],
  data() {
    return {
      store: useStore(),
      screenWidth: document.body.clientWidth,
      screenHeight: document.body.clientHeight,
      pageWidth: 960, //
      pageHeight: 1020, //
      textareaWidth: 720, //
      textareaHeight: 900, //
      popoverWidth: 342, //
      pageTurning: "", //
      sizeSettings: {
        fontFamily: "Arial",
        fontSize: 18,
        // fontWeight:'+px',
        // fontStyle: 'italic',
        lineHeight: 1.5, //
        paraSpace: 0.5, //
        titleScale: 2, //
        paddingTop: 20,
        paddingBottom: 20,
        paddingLR: 20,
        widthPercent: 50,
      },
      colorSettings: {
        textColor: "#000000",
        bgColor: "#EFEFEF",
        borderColor: "#FAFAFA",
      },
      //
      pageList: [], //
      noIndent: [], //
      currentChapter: 0, //0
      currentPage: 0, //0
      currentPara: 0, //()0
      //
      readingChapter: 0, //0
      readingPage: 0, //0
      readingPara: 0, //0
      //
      bookmarks: [], //
      lastPosition: { chapter: 0, para: 0 }, //
      notes: [], //
      noteRemark: "", //
      noteColor: "", //
      // remarkRows: 2, //
      //
      progress: 0, //
      keywords: "", //
      tempParaIndex: null, //
      tempStartIndex: -1, //
      //
      catalogRenderMax: 100, //
      catalogScrollPercent: 0, //
      catalogBeforeHeight: 0, //
      catalogAfterHeight: 0, //
      //
      timer: null,
      messageTimer: null,
      wheelTimer: null,
      sizeSettingsTimer: null,
      speechOptionTimer: null,
      timedSpeechInterval: null,
      autoSpeechTimer: null,
      screenSizeTimer: null,
      //
      device: "desktop",
      menuVisible: false, //
      menuOffsetX: 0, //X
      menuOffsetY: 0, //Y
      menuPlacement: "bottom", //
      settingsVisible: false, //
      catalogVisible: false, //
      voiceVisible: false, //
      searchVisible: false, //
      progressVisible: false, //
      isWheelLocked: false, //
      turnLocked: false, //()
      // isMouseDown: false, //
      //
      utterance: window.SpeechSynthesisUtterance ? new SpeechSynthesisUtterance() : null,
      voiceList: [],
      voiceIndex: "",
      speechOptions: {
        rate: 1,
        volume: 1,
        pitch: 1,
        voiceIndex: 0,
      },
      isSpeaking: false, //
      isPaused: false, //(speechSynthesis.pausedfalse)
      speechTimeLeft: 60, //
      timedSpeechOpen: false, //
      readingStatus: {
        paraIndex: -1,
        chapterIndex: -1,
        time: 0,
      }, //
    }
  },
  created() {
    // var url = "http://192.168.0.113:8080/#/article/book"
    // window.open(
    //   url,
    //   "readerWindow",
    //   "alwaysRaised=yes,z-look=yes,toolbar=no,location=no,resizable=no,height=960,width=800,,scrollbars=yes,left=380,top=300"
    // )
  },
  beforeMount() {},
  mounted() {
    const sizeSettings = localStorage.getItem("SIZE_SETTINGS")
    if (sizeSettings && sizeSettings.length) this.sizeSettings = JSON.parse(sizeSettings) //
    const colorSettings = localStorage.getItem("COLOR_SETTINGS")
    if (colorSettings && colorSettings.length) this.colorSettings = JSON.parse(colorSettings) //
    const pageTurning = localStorage.getItem("PAGE_TURNING")
    if (pageTurning && pageTurning.length) this.pageTurning = pageTurning //
    const speechOptions = localStorage.getItem("SPEECH_OPTIONS")
    if (speechOptions && speechOptions.length) this.speechOptions = JSON.parse(speechOptions) //
    this.voiceIndex = this.speechOptions.voiceIndex
    this.device = this.store.state.app.device
    this.pageInit()
    const lastPosition = localStorage.getItem(
      "LASTPOSITION_" + this.bookId,
      JSON.stringify(this.lastPosition)
    )
    if (lastPosition) {
      this.lastPosition = JSON.parse(lastPosition)
      this.currentChapter = this.lastPosition.chapter
      this.currentPage = this.getPageIndex(this.lastPosition.para)
    }

    //
    window.onresize = () => {
      return (() => {
        this.screenWidth = document.body.clientWidth
        this.screenHeight = document.body.clientHeight
      })()
    }
    // 
    document.onkeydown = () => {
      // thisfunctionvar that = this
      // lambda
      switch (window.event.keyCode) {
        case 37:
          this.handleDebounce(this.prevPage)
          break
        case 38:
          this.handleDebounce(this.prevChapter)
          break
        case 39:
          this.handleDebounce(this.nextPage)
          break
        case 40:
          this.handleDebounce(this.nextChapter)
          break
      }
    }
    // document.onmousedown = () => (this.isMouseDown = true)
    // document.onmouseup = () => (this.isMouseDown = false)
    //
    this.$refs.readerBody.oncontextmenu = function () {
      return false
    }
    //onscroll
    const setRenderArea = this.handleThrottle(this.setRenderArea, 100)
    this.$refs.catalogPane.onscroll = () => {
      setRenderArea()
    }
    // window.onerror = function (message, source, line, column, error) {
    //   alert(message)
    // }
    // console.log("", this.measureText("", 20))
  },
  methods: {
    //#region 
    // 
    pageInit() {
      if (this.device == "desktop") {
        const percent = this.sizeSettings.widthPercent ? this.sizeSettings.widthPercent : 50
        this.pageWidth = this.screenWidth * (percent / 100)
        this.pageHeight = window.innerHeight - 220
      } else if (this.store.state.app.device == "mobile") {
        this.pageWidth = this.screenWidth * 0.95
        this.pageHeight = window.screen.height - 100
        // alert(this.textareaWidth + "," + this.pageHeight + "," + this.textareaHeight)
      }
      //
      this.textareaWidth = this.pageWidth - this.sizeSettings.paddingLR * 2
      if (this.pageTurning == "") {
        this.textareaHeight = 100000
      } else {
        this.textareaHeight =
          this.pageHeight - this.sizeSettings.paddingTop - this.sizeSettings.paddingBottom
      }
      console.log(
        `:${this.pageWidth}(${this.textareaWidth}) , :${this.pageHeight}(${this.textareaHeight})`
      )
    },
    // 
    setRenderArea() {
      const h = 30
      const max = this.chapterList.length * h
      const scrollTop = this.$refs.catalogPane.scrollTop
      const len = this.chapterListRendered.length * h
      const before = Math.max(scrollTop - len / 2, 0)
      const after = max - before - len
      // console.log(before, len, after, "before + len + after =", before + len + after)
      this.catalogScrollPercent = scrollTop / max
      this.catalogBeforeHeight = before
      this.catalogAfterHeight = after > 0 ? after : 150
      this.$nextTick(() => {
        this.$refs.catalogPane.scrollTop = scrollTop
      })
    },
    // 
    handleDebounce(fn, wait = 100) {
      clearTimeout(this.timer) //
      this.timer = setTimeout(() => {
        fn()
      }, wait)
    },
    // 
    handleThrottle(fn, wait = 100) {
      let lock = true
      return function () {
        if (lock) {
          lock = false //
          setTimeout(() => {
            fn()
            lock = true //
          }, wait)
        }
      }
    },
    // 
    combineFn(fns, args) {
      fns.forEach((fn, index) => fn(...args[index]))
    },
    // $message
    showMessage(obj) {
      clearTimeout(this.messageTimer) //
      this.messageTimer = setTimeout(() => this.$message(obj), 200)
    },
    // 
    fullscreen(className) {
      // div
      let fullarea = document.getElementsByClassName(className)[0]
      if (!document.fullscreenElement) {
        // 
        fullarea.requestFullscreen()
        // alert(fullarea)
      } else {
        // 
        if (document.exitFullscreen) {
          document.exitFullscreen()
        }
      }
    },
    //
    resetStyle() {
      this.sizeSettings = {
        fontFamily: "Arial",
        fontSize: 18,
        lineHeight: 1.5, //
        paraSpace: 0.5, //
        titleScale: 2, //
        paddingTop: 20,
        paddingBottom: 20,
        paddingLR: 20,
        widthPercent: 50,
      }
      this.colorSettings = {
        textColor: "#000000",
        bgColor: "#EFEFEF",
        borderColor: "#FAFAFA",
      }
    },
    //#endregion

    //#region 
    //
    prevChapter() {
      if (this.currentChapter > 0) {
        this.currentChapter--
        this.clearSelection()
      } else {
        this.showMessage({
          message: "",
          type: "info",
          duration: 1000,
        })
      }
    },
    // 
    firstPage() {
      // eslint-disable-next-line no-unused-expressions
      this.currentPage = 0
      this.clearSelection()
    },
    // 
    prevPage() {
      if (this.currentPage === 0) {
        if (this.currentChapter > 0) {
          this.currentChapter--
          this.$nextTick(() => {
            this.lastPage()
            this.clearSelection()
          })
        } else {
          this.showMessage({
            message: "",
            type: "info",
            duration: 1000,
          })
        }
      } else {
        this.currentPage--
        this.clearSelection()
      }
    },
    // 
    nextPage() {
      if (this.currentPage === this.pageTotal - 1) {
        if (this.currentChapter < this.chapterList.length - 1) {
          this.currentChapter++
          this.clearSelection()
        } else {
          this.showMessage({
            message: "",
            type: "info",
            duration: 1000,
          })
        }
        // return false
      } else {
        this.currentPage++
        this.clearSelection()
      }
    },
    // 
    lastPage() {
      if (this.currentPage === this.pageTotal - 1) {
        return false
      } else {
        this.currentPage = this.pageTotal - 1
        this.clearSelection()
      }
    },
    //
    nextChapter() {
      if (this.currentChapter < this.chapterList.length - 1) {
        this.currentChapter++
        this.clearSelection()
      } else {
        this.showMessage({
          message: "",
          type: "info",
          duration: 1000,
        })
      }
    },
    jumpTo(val) {
      this.currentChapter = Math.floor(((this.chapterList.length - 1) * val) / 100)
      this.clearSelection()
    },
    //
    wheelHandle(e) {
      if (!this.isWheelLocked && this.pageTurning != "") {
        const debounce = 50
        if (e.deltaY > 0) {
          // wheelDelta <0
          clearTimeout(this.wheelTimer) //
          this.wheelTimer = setTimeout(() => this.nextPage(), debounce)
        } else if (e.deltaY < 0) {
          // wheelDelta >0
          clearTimeout(this.wheelTimer) //
          this.wheelTimer = setTimeout(() => this.prevPage(), debounce)
        }
      }
    },
    //
    animatePage(reverse) {
      let parent = document.querySelector(".reader-container")
      let page = document.querySelector(".reader-body")
      let oldPage = page.cloneNode(1) //
      const width = this.pageWidth
      oldPage.style.position = "absolute"
      oldPage.style.width = width + "px"
      oldPage.style.left = "10px"
      oldPage.style.top = "30px"
      oldPage.style.boxShadow = (reverse ? -1 : 1) * 6 + "px 0px 6px 1px rgb(128 131 137 / 25%)"
      // oldPage.style.transform = "rotateX(30deg)"
      parent.appendChild(oldPage) //DOM
      let timeout //
      let keyframes //
      if (this.pageTurning == "") {
        timeout = 400 //
        const deltaX = (reverse ? 1 : -1) * width
        keyframes = [
          { transform: "translateX(0px)" },
          { transform: "translateX(" + deltaX * 0.5 + "px)" },
          { transform: "translateX(" + deltaX * 0.9 + "px)" },
          { transform: "translateX(" + deltaX + "px)" },
        ]
      } else if (this.pageTurning == "") {
        timeout = 400 //
        const deltaX = (reverse ? 1 : -1) * width
        keyframes = [
          { transform: "translateX(0px)" },
          { transform: "translateX(" + deltaX * 0.5 + "px)" },
          { transform: "translateX(" + deltaX * 0.9 + "px)" },
          { transform: "translateX(" + deltaX + "px)" },
        ]
        //DOM
        this.$nextTick(() => {
          let newPage = page.cloneNode(1) //
          newPage.style.position = "absolute"
          newPage.style.width = width + "px"
          newPage.style.left = (reverse ? -1 : 1) * width + 10 + "px"
          newPage.style.top = "30px"
          parent.appendChild(newPage) //DOM
          // 
          newPage.animate(keyframes, {
            duration: timeout, //  ()
            iterations: 1, //  (Infinity)
            fill: "forwards", // 
            delay: 0, //  ()
            easing: "ease-out", //   (linear: )
          })
          setTimeout(() => {
            parent.removeChild(newPage) //
          }, timeout)
        })
      } else if (this.pageTurning == "") {
        timeout = 1200 //
        oldPage.style.transformOrigin = reverse ? "right" : "left"
        keyframes = reverse
          ? [
              { transform: "rotateY(0)" },
              { transform: "rotateY(100deg)" },
              { transform: "rotateY(180deg)" },
            ]
          : [
              { transform: "rotateY(0deg)" },
              { transform: "rotateY(-100deg)" },
              { transform: "rotateY(-180deg)" },
            ]
      }
      //
      oldPage.animate(keyframes, {
        duration: timeout, //  ()
        iterations: 1, //  (Infinity)
        fill: "forwards", // 
        delay: 0, //  ()
        easing: "ease-out", //   (linear: )
      })
      setTimeout(() => {
        parent.removeChild(oldPage) //
      }, timeout)
    },
    //#endregion

    //#region 
    // 
    closePopover() {
      this.menuVisible = false
      this.settingsVisible = false
      this.voiceVisible = false
      this.searchVisible = false
      this.progressVisible = false
      this.closeCatalog()
      this.$refs.readerBody.oncontextmenu = function () {
        return false //
      }
    },
    // 
    handleGoBack() {
      this.menuVisible = false
      this.settingsVisible = false
      this.voiceVisible = false
      this.searchVisible = false
      this.progressVisible = false
      this.closeCatalog() //
      this.speechStop() //
      this.changeMode()
    },
    // 
    openCatalog() {
      // const timeStart = performance.now()
      this.menuVisible = false
      this.settingsVisible = false
      this.voiceVisible = false
      this.searchVisible = false
      this.progressVisible = false
      this.catalogVisible = true
      this.isWheelLocked = true
      //
      this.$nextTick(() => {
        //
        const percent = this.currentChapter / this.chapterList.length
        //30height() + margin
        const scrollMax = this.chapterList.length * 30
        //1/3
        this.$refs.catalogPane.scrollTop = percent * scrollMax - (this.pageHeight - 120) / 3
      })
      //
      const bookmarks = localStorage.getItem("BOOKMARKS_" + this.bookId)
      if (bookmarks) this.bookmarks = JSON.parse(bookmarks)
      // console.log(performance.now() - timeStart)
    },
    // 
    selectChapter(val) {
      this.currentChapter = val
      this.closeCatalog()
    },
    // 
    openSettings() {
      this.settingsVisible = !this.settingsVisible
      this.menuVisible = false
      this.voiceVisible = false
      this.searchVisible = false
      this.progressVisible = false
    },
    // 
    openVoice() {
      this.voiceVisible = !this.voiceVisible
      this.menuVisible = false
      this.settingsVisible = false
      this.searchVisible = false
      this.progressVisible = false
      this.voiceInit()
    },
    // 
    voiceInit() {
      if (this.speechOptions && this.speechOptions.voiceIndex) {
        this.voiceIndex = this.speechOptions.voiceIndex
      }
      if (!this.voiceList || this.voiceList.length == 0) {
        this.getVoiceList()
      }
    },
    // 
    openSearch() {
      this.searchVisible = !this.searchVisible
      this.menuVisible = false
      this.voiceVisible = false
      this.settingsVisible = false
      this.progressVisible = false
    },
    // 
    openProgress() {
      this.progressVisible = !this.progressVisible
      this.menuVisible = false
      this.voiceVisible = false
      this.settingsVisible = false
      this.searchVisible = false
    },
    // 
    closeCatalog() {
      this.catalogVisible = false
      this.isWheelLocked = false
      this.searchVisible = false
    },
    // 
    handleSearch(keywords, reverse = false) {
      let index = -1
      let paraIndex = 0
      let chapterIndex = this.currentChapter
      let paras = this.list //this.chapterList[chapterIndex] ? this.chapterList[chapterIndex].content : []
      //null >= 0  true
      if (this.tempParaIndex && this.tempParaIndex >= 0) {
        paraIndex = this.tempParaIndex
      } else if (reverse && this.tempParaIndex == -1) {
        chapterIndex -= 1
        paras = this.chapterList[chapterIndex] ? this.chapterList[chapterIndex].content : []
        paraIndex = paras.length - 1
      } else {
        paraIndex = this.getParaIndexCh(0)
      }
      while (chapterIndex < this.chapterList.length && chapterIndex >= 0) {
        while (paraIndex < paras.length && paraIndex >= 0) {
          //
          if (reverse) {
            const start =
              this.tempStartIndex == -1 ? paras[paraIndex].length - 1 : this.tempStartIndex
            index = paras[paraIndex].lastIndexOf(keywords, start - 1)
          } else {
            index = paras[paraIndex].indexOf(keywords, this.tempStartIndex + 1)
          }
          //
          if (index > -1) {
            this.tempStartIndex = index
            //
            this.currentChapter = chapterIndex
            //DOM
            this.$nextTick(() => {
              //
              this.currentPage = this.getPageIndex(paraIndex)
              //DOM
              this.$nextTick(() => {
                const paraIndexOfPage = this.getParaIndex(paraIndex)
                let startOffset = index
                let endOffset = index + keywords.length
                const paraLength = this.pageList[this.currentPage][paraIndexOfPage].length
                //
                if (endOffset > paraLength) {
                  //
                  if (startOffset < paraLength) {
                    endOffset = paraLength //
                    //
                  } else {
                    //
                    this.currentPage++
                    //DOM
                    this.$nextTick(() => {
                      const selector = ".main-para:nth-child(1)"
                      this.getSelection(selector, startOffset - paraLength, endOffset - paraLength)
                    })
                    //nextTick
                    this.tempParaIndex = paraIndex // + (reverse ? -1 : 1)
                    return //nextTick
                  }
                }
                const selector = ".main-para:nth-child(" + (paraIndexOfPage + 1) + ")"
                this.getSelection(selector, startOffset, endOffset)
                if (this.pageTurning === "") {
                  this.$el
                    .querySelector(selector)
                    .scrollIntoView({ behavior: "smooth", block: "center" })
                }
              })
            })
            //nextTick
            if (reverse && paras[paraIndex].lastIndexOf(keywords, index - 1) == index) {
              this.tempParaIndex = paraIndex - 1
            } else {
              this.tempParaIndex = paraIndex // + (reverse ? -1 : 1)
            }
            return //nextTick
          } else {
            paraIndex += reverse ? -1 : 1
            this.tempStartIndex = -1
          }
        }
        chapterIndex += reverse ? -1 : 1
        if (chapterIndex == this.chapterList.length) {
          this.showMessage({ message: "", type: "info", duration: 1000 })
        } else if (chapterIndex < 0) {
          this.showMessage({ message: "", type: "info", duration: 1000 })
        } else {
          paras = this.chapterList[chapterIndex] ? this.chapterList[chapterIndex].content : []
          paraIndex = reverse ? paras.length - 1 : 0
        }
      }
    },
    // 
    handleMouseup(e) {
      //
      if (e.button == 2) {
        this.openPopupMenu(e)
      }
    },
    // 
    openPopupMenu(e) {
      this.settingsVisible = false
      this.voiceVisible = false
      this.searchVisible = false
      this.progressVisible = false
      this.catalogVisible = false
      // e.offsetX, e.offsetY, e.pageX, e.pageY, e.clientX, e.clientY
      // console.log(e.layerX, e.layerY)
      if (e.layerX || e.layerY) {
        this.menuOffsetX = e.layerX
        this.menuOffsetY = e.layerY
        const menuW = 240
        const menuH = 360
        if (e.layerY > this.pageHeight - menuH) {
          if (e.layerX > this.pageWidth - menuW) this.menuPlacement = "top-end"
          else this.menuPlacement = "top-start"
        } else {
          if (e.layerX > this.pageWidth - menuW) this.menuPlacement = "bottom-end"
          else this.menuPlacement = "bottom-start"
        }
      }
      const search = this.searchMarkedPara(this.currentPara)
      if (search > -1) {
        this.noteRemark = this.notes[search].remark
        this.noteColor = this.notes[search].color
      } else {
        this.noteRemark = ""
        if (this.noteColor == "") this.noteColor = this.markedColors[0]
      }
      this.menuVisible = true
      this.voiceInit()
    },
    // 
    closePopupMenu() {
      this.menuVisible = false
    },
    // 
    searchMarkedPara(index) {
      for (let i in this.notes) {
        const page = this.getPageIndex(this.notes[i].para)
        const para = this.getParaIndex(this.notes[i].para)
        if (
          this.notes[i].chapter == this.currentChapter &&
          ((page == this.currentPage && para == index) ||
            (this.noIndentPara != "" &&
              index == 0 &&
              page == this.currentPage - 1 &&
              this.getPageIndex(this.notes[i].para + 1) == this.currentPage))
        ) {
          return i
        }
      }
      return -1
    },
    //
    addNote(index, remark = "", color = this.markedColors[0], update = false) {
      // const notes = localStorage.getItem("NOTES_" + this.bookId)
      // if (notes) this.notes = JSON.parse(notes)
      const search = this.searchMarkedPara(index)
      if (search > -1) {
        if (update || remark != "") {
          let note = this.notes[search]
          note.remark = remark
          note.color = color
          this.updateNote(search, note)
        } else this.deleteNote(search)
      } else {
        const note = {
          chapter: this.currentChapter,
          para: this.getParaIndexCh(index),
          time: new Date().toLocaleDateString().replaceAll("/", "-"),
          remark: remark,
          color: color,
        }
        this.notes.push(note)
        localStorage.setItem("NOTES_" + this.bookId, JSON.stringify(this.notes))
        this.setTextStyle()
        this.showMessage({ message: "", type: "success", duration: 1000 })
      }
      this.closePopover()
    },
    //
    updateNote(index, note) {
      this.notes.splice(index, 1, note)
      localStorage.setItem("NOTES_" + this.bookId, JSON.stringify(this.notes))
      this.setTextStyle()
    },
    //
    deleteNote(index) {
      this.notes.splice(index, 1)
      if (this.notes.length > 0) {
        localStorage.setItem("NOTES_" + this.bookId, JSON.stringify(this.notes))
      } else {
        //
        localStorage.removeItem("NOTES_" + this.bookId)
      }
      this.setTextStyle()
    },
    handleDeleteNote(para) {
      this.deleteNote(this.searchMarkedPara(para))
      this.closePopover()
    },
    //
    jumpToNote(index) {
      console.log(index, this.notes[index].chapter, this.notes[index].para)
      this.currentChapter = this.notes[index].chapter
      this.$nextTick(() => {
        console.log(this.getPageIndex(this.notes[index].para))
        this.currentPage = this.getPageIndex(this.notes[index].para)
      })
      this.closeCatalog()
    },
    //
    getNotePreview(index) {
      const paras = this.chapterList[this.notes[index].chapter].content
      const paraIndex = this.notes[index].para
      let result = ""
      if (paras[paraIndex].length < 40) {
        result = paras[paraIndex] + "..."
      } else {
        result = paras[paraIndex].slice(0, 40) + "..."
      }
      return result
    },
    //
    addBookmark() {
      const bookmarks = localStorage.getItem("BOOKMARKS_" + this.bookId)
      if (bookmarks) this.bookmarks = JSON.parse(bookmarks)
      const start = this.hasNoIndentPara() && this.pageList[this.currentPage].length > 0 ? 1 : 0
      const bookmark = {
        chapter: this.currentChapter,
        para: this.getParaIndexCh(start),
        time: new Date().toLocaleDateString().replaceAll("/", "-"),
      }
      this.bookmarks.push(bookmark)
      localStorage.setItem("BOOKMARKS_" + this.bookId, JSON.stringify(this.bookmarks))
      this.showMessage({ message: "", type: "success", duration: 1000 })
      this.closePopover()
    },
    //
    deleteBookmark(index) {
      this.bookmarks.splice(index, 1)
      if (this.bookmarks.length > 0) {
        localStorage.setItem("BOOKMARKS_" + this.bookId, JSON.stringify(this.bookmarks))
      } else {
        //
        localStorage.removeItem("BOOKMARKS_" + this.bookId)
      }
    },
    //
    jumpToBookmark(index) {
      console.log(index, this.bookmarks[index].chapter, this.bookmarks[index].para)
      this.currentChapter = this.bookmarks[index].chapter
      this.$nextTick(() => {
        console.log(this.getPageIndex(this.bookmarks[index].para))
        this.currentPage = this.getPageIndex(this.bookmarks[index].para)
      })
      this.closeCatalog()
    },
    //
    getBookmarkPreview(index) {
      const paras = this.chapterList[this.bookmarks[index].chapter].content
      const paraIndex = this.bookmarks[index].para
      let result = ""
      if (paras[paraIndex].length < 20) {
        result = paras[paraIndex]
        if (paras[paraIndex + 1]) {
          result += "\r\n" + paras[paraIndex + 1].slice(0, 20)
        }
        result += "..."
      } else {
        result = paras[paraIndex].slice(0, 40) + "..."
      }
      return result
    },
    //#endregion

    //#region 
    // 
    setTextStyle() {
      const pTags = Array.from(this.$el.querySelector(".reader-body").getElementsByTagName("p"))
      pTags.forEach((p, index) => {
        p.innerHTML = this.clearStyle(p.innerHTML)
        const search = this.searchMarkedPara(index)
        if (search > -1) {
          const color = this.notes[search].color ?? this.markedColors[0]
          p.innerHTML = this.insertStyle(p.innerHTML, "background-color:" + color)
          // p.innerHTML = p.innerHTML.fontcolor("Red")
        }
      })
    },
    insertStyle(text, style) {
      return `<span style="${style}" key="0">${text}</span>`
    },
    clearStyle(text) {
      // const reg1 = /^(<span style=".*" key="0">)|(<font .*=".*">) /g
      // const reg2 = /(<\/span>)|(<\/font>)$/g
      const reg1 = /^(<span style=".*" key="0">)/g
      const reg2 = /(<\/span>)$/g
      return text.replace(reg1, "").replaceAll(reg2, "")
    },
    // 
    clearSelection() {
      window.getSelection().removeAllRanges()
    },
    // 
    getSelection(selector, startOffset, endOffset) {
      //$nextTick2022-12-05
      this.$nextTick(() => {
        let elm = this.$el.querySelector(selector) //
        let range = document.createRange()
        let startNode = elm.firstChild //
        //
        // if (startNode.nodeType !== 3) startNode = startNode.firstChild
        range.setStart(startNode, startOffset)
        range.setEnd(startNode, endOffset)
        let selection = window.getSelection()
        selection.removeAllRanges()
        selection.addRange(range)
        range.detach() //
        range = null //
      })
    },
    // 
    getTitle() {
      if (this.chapterList && this.chapterList[this.currentChapter]) {
        return this.chapterList[this.currentChapter].title
      } else {
        return this.articleData.title
      }
    },
    // 
    getParaFontSize(index) {
      if (index == 0 && this.currentPage == 0) {
        return this.sizeSettings.fontSize * this.sizeSettings.titleScale
      } else return this.sizeSettings.fontSize
    },
    // 
    getCatalogWidth() {
      if (this.pageWidth < 400) return this.pageWidth
      else return 400
    },
    // 
    getPageWordNum() {
      if (this.currentPage < 0) return 0
      const lenArray = []
      const currentArray = this.pageList[this.currentPage]
      //Vue(Proxy)map,reduce
      for (let i in currentArray) {
        lenArray.push(currentArray[i].length)
      }
      return lenArray.reduce((sum, pre) => sum + pre, 0)
      // return this.pageList[this.currentPage].reduce((sum, pre) => sum + pre.length, 0)
    },
    // 
    getTotalWordNum() {
      return this.pageList.reduce(
        (sum, pre) => sum + pre.reduce((sum, pre) => sum + pre.length, 0),
        0
      )
    },
    // 
    isNoIndent(pageIndex) {
      if (!this.noIndent) return false
      for (let i in this.noIndent) {
        if (pageIndex == this.noIndent[i]) return true
      }
      return false
    },
    // 
    hasNoIndentPara() {
      if (!(!this.noIndentPara.length || this.noIndentPara.length == 0)) return true
      return false
    },
    //#endregion

    //#region 
    /** @description  */
    getParaIndexCh(index) {
      // if (this.pageList.length == 0) return 0
      let tempIndex = index
      for (let i = 0; i <= this.currentPage; i++) {
        //1
        if (this.isNoIndent(i)) {
          tempIndex--
        }
        tempIndex += (this.pageList[i] ?? "").length
      }
      tempIndex -= (this.pageList[this.currentPage] ?? []).length
      return tempIndex
    },
    /** @description  */
    getParaIndex(paraIndex) {
      let tempIndex = paraIndex
      for (let i = 0; i < this.pageList.length; i++) {
        //1
        if (this.isNoIndent(i)) {
          tempIndex++
        }
        tempIndex -= this.pageList[i].length
        if (tempIndex < 0) return tempIndex + this.pageList[i].length
      }
    },
    /** @description  */
    getPageIndex(paraIndex) {
      let tempIndex = paraIndex
      for (let i = 0; i < this.pageList.length; i++) {
        //1
        if (this.isNoIndent(i)) {
          tempIndex++
        }
        tempIndex -= this.pageList[i].length
        if (tempIndex < 0) return i
      }
    },
    // 
    getClosedColor(color, delta = 10) {
      const adjust = (base, variation) => {
        let mix = base + variation
        if (mix < 0 || mix > 255) mix = base - variation
        return mix
      }
      let rgb = color.replace(/#/g, "").match(/[\da-z]{2}/gi)
      rgb = rgb.map((item) => adjust(parseInt(item, 16), delta))
      let result = "#" + rgb.reduce((sum, pre) => sum + ("0" + pre.toString(16)).slice(-2), "")
      // console.log(result)
      return result
    },
    // 
    reverseColor(color) {
      let colorValue = "0x" + color.replace(/#/g, "")
      let str = "000000" + (0xffffff - colorValue).toString(16)
      return "#" + str.substring(str.length - 6, str.length)
    },
    // 
    reverseAvgColor(colorA, colorB) {
      let valueA = "0x" + colorA.replace(/#/g, "")
      let valueB = 0xffffff - ("0x" + colorB.replace(/#/g, ""))
      let str = "000000" + (0xffffff - ((valueA + valueB) / 2).toFixed(0)).toString(16)
      return "#" + str.substring(str.length - 6, str.length)
    },
    // ()
    getRestText(paraIndex, pageIndex = this.currentPage) {
      let text = ""
      this.pageList.forEach((page, index) => {
        if (index >= pageIndex) {
          page[index].forEach((para, index) => {
            if (index >= paraIndex) text += para
          })
        }
      })
      return text
    },
    /** @description ... */
    limitText(text = "", limit) {
      return text.length > limit ? text.slice(0, limit) + "..." : text
    },
    // 
    getCurrentParaText() {
      const chapter = this.chapterList[this.currentChapter]
      if (!chapter) return ""
      const para = this.getParaIndexCh(this.currentPara)
      return chapter.content[para]
    },
    //#endregion
  },

  //#region 
  computed: {
    // 
    list: {
      get() {
        //return
        if (this.chapterList && this.chapterList[this.currentChapter]) {
          return this.chapterList[this.currentChapter].content
        } else if (this.articleData && this.articleData.content) {
          return this.articleData.content
        } else return ""
      },
    },
    // 
    pageTotal: function () {
      return this.pageList.length
    },
    // 
    noIndentPara: function () {
      if (this.isNoIndent(this.currentPage)) {
        return this.pageList[this.currentPage][0]
      }
      return ""
    },
    // 
    indentParas: function () {
      // let newArr = this.pageList[this.currentPage] //
      let newArr = []
      for (let i in this.pageList[this.currentPage]) {
        newArr.push(this.pageList[this.currentPage][i])
      }
      if (this.isNoIndent(this.currentPage)) newArr.shift()
      return newArr
    },
    chapterListRendered: function () {
      // const t1 = performance.now()
      const total = this.chapterList.length
      const len = this.catalogRenderMax //
      const start = Math.max(Math.floor(this.catalogScrollPercent * total) - len / 2, 0)
      let newList = []
      for (let i = start; i < start + len && i < total; i++) {
        newList.push({ id: this.chapterList[i].id, title: this.chapterList[i].title })
      }
      // console.log(performance.now() - t1)
      return newList
    },
  },
  //#endregion

  //#region 
  watch: {
    inReader: {
      handler(val) {
        if (val) {
          //
          const lastPosition = localStorage.getItem(
            "LASTPOSITION_" + this.bookId,
            JSON.stringify(this.lastPosition)
          )
          if (lastPosition) {
            this.lastPosition = JSON.parse(lastPosition)
            this.currentChapter = this.lastPosition.chapter
            this.$nextTick(() => {
              this.currentPage = this.getPageIndex(this.lastPosition.para)
            })
          } else {
            this.currentChapter = 0
            this.currentPage = 0
          }
        } else {
          //
          const start = this.hasNoIndentPara() && this.pageList[this.currentPage].length > 0 ? 1 : 0
          this.lastPosition = { chapter: this.currentChapter, para: this.getParaIndexCh(start) }
          localStorage.setItem("LASTPOSITION_" + this.bookId, JSON.stringify(this.lastPosition))
        }
      },
    },
    list: {
      handler(val) {
        this.repaging(val) //
      },
    },
    chapterList: {
      handler() {
        //
        const notes = localStorage.getItem("NOTES_" + this.bookId)
        if (notes) this.notes = JSON.parse(notes)
        const bookmarks = localStorage.getItem("BOOKMARKS_" + this.bookId)
        if (bookmarks) this.bookmarks = JSON.parse(bookmarks)
        //
        const after = this.chapterList.length - this.chapterListRendered.length
        this.catalogAfterHeight = after * 30
      },
      immediate: true, //
    },
    bookId: {
      handler() {
        this.bookmarks = [] //
        this.notes = [] //
      },
    },
    sizeSettings: {
      handler() {
        clearTimeout(this.sizeSettingsTimer) //
        this.sizeSettingsTimer = setTimeout(() => {
          this.pageInit()
          this.repage(this.list) //
          localStorage.setItem("SIZE_SETTINGS", JSON.stringify(this.sizeSettings))
        }, 500)
      },
      deep: true, //
    },
    colorSettings: {
      handler() {
        localStorage.setItem("COLOR_SETTINGS", JSON.stringify(this.colorSettings))
      },
      deep: true, //
    },
    speechOptions: {
      handler() {
        clearTimeout(this.speechOptionTimer) //
        this.speechOptionTimer = setTimeout(() => {
          this.changeVoice()
          localStorage.setItem("SPEECH_OPTIONS", JSON.stringify(this.speechOptions))
        }, 500)
      },
      deep: true, //
    },
    screenWidth() {
      clearTimeout(this.screenSizeTimer) //
      this.screenSizeTimer = setTimeout(() => {
        this.pageInit()
        this.repage(this.list) //
      }, 500)
    },
    screenHeight() {
      clearTimeout(this.screenSizeTimer) //
      this.screenSizeTimer = setTimeout(() => {
        this.pageInit()
        this.repage(this.list) //
      }, 500)
    },
    currentChapter(newValue, oldValue) {
      // this.closePopover()
      // this.$nextTick(() => this.setTextStyle())
      this.progress = parseFloat(((newValue / this.chapterList.length) * 100).toFixed(1))
      if (this.pageTurning == "") {
        this.$refs.readerBody.scrollTop = 0
      } else if ("".includes(this.pageTurning) && !this.turnLocked) {
        this.turnLocked = true
        this.animatePage(newValue < oldValue)
        setTimeout(() => {
          this.turnLocked = false
        }, 100)
      }
    },
    currentPage(newValue, oldValue) {
      // this.closePopover()
      // this.$nextTick(() => this.setTextStyle())
      if ("".includes(this.pageTurning) && !this.turnLocked) {
        this.animatePage(newValue < oldValue)
      }
    },
    indentParas() {
      this.$nextTick(() => this.setTextStyle())
    },
    pageTurning(newValue, oldValue) {
      if (newValue == "" || oldValue == "") {
        this.pageInit()
        this.repage(this.list)
      }
      localStorage.setItem("PAGE_TURNING", this.pageTurning)
    },
  },
  //#endregion
}
</script>

