//
.catalog-container {
  @include pretty-scrollbar;
  position: absolute;
  // width: calc(50% - 20px);
  // min-width: 320px;
  max-width: 600px;
  padding: 10px 0px 10px 0px;
  resize: horizontal;
  background-color: #8aa6b9;
  // box-shadow: var(--el-box-shadow-light);
  box-shadow: 2px 0px 4px 1px rgb(128 131 137 / 30%); //    
  border-radius: 10px;
  opacity: 0.99;
  text-align: left;
  .catalog-button {
    margin-left: 10px;
    &:hover {
      color: #1e90ff;
    }
  }
  .catalog-title {
    margin-left: 10px;
