export default {
  methods: {
    //#region 

    /** @description  */
    getFocusColor(colorA, colorB, colorC = "#DD66CC", delta = 10) {
      const adjust = (base, variation) => {
        let mix = base + variation
        if (mix < 0 || mix > 255) mix = base - variation
        return mix
      }
      const fix = (a, b, c) => {
        if (Math.abs(a - c) < delta) c = adjust(a, delta)
        if (Math.abs(b - c) < delta) c = adjust(c, delta)
        return c
      }
      let rgbA = colorA.replace(/#/g, "").match(/[\da-z]{2}/gi)
      let rgbB = colorB.replace(/#/g, "").match(/[\da-z]{2}/gi)
      let rgbC = colorC.replace(/#/g, "").match(/[\da-z]{2}/gi)
      rgbA = rgbA.map((item) => parseInt(item, 16))
      rgbB = rgbB.map((item) => parseInt(item, 16))
      rgbB = rgbC.map((item) => parseInt(item, 16))
      let rgb = [
        fix(rgbA[0], rgbB[0], rgbC[0]),
        fix(rgbA[1], rgbB[1], rgbC[1]),
        fix(rgbA[2], rgbB[2], rgbC[2]),
      ]
      // console.log(rgb)
      let result = "#" + rgb.reduce((sum, pre) => sum + ("0" + pre.toString(16)).slice(-2), "")
      // console.log(result)
      return result
    },

    /** @description  */
    getParaTextColor(index) {
      //isSpeakingspeechSynthesis.speakingVue
      //speechSynthesis.speaking
      if (
        this.isSpeaking &&
        this.readingPara == this.getParaIndexCh(index) &&
        this.readingChapter == this.currentChapter
      ) {
        return this.getFocusColor(
          this.colorSettings.textColor,
          this.colorSettings.bgColor,
          "#DD66CC"
        )
      }
    },

    /** @description  */
    getTextShadow(index) {
      if (
        this.isSpeaking &&
        this.readingPara == this.getParaIndexCh(index) &&
        this.readingChapter == this.currentChapter
      ) {
        return "1px 1px 1px #999999"
      } else {
        return "0 0 0"
      }
    },

    /** @description  */
    getParaBgColor(index) {
      if (
        this.isSpeaking &&
        this.readingPara == this.getParaIndexCh(index) &&
        this.readingChapter == this.currentChapter
      ) {
        return this.getClosedColor(this.colorSettings.bgColor, -20)
      }
    },

    /** @description  */
    timedSpeech() {
      if (this.timedSpeechOpen) {
        this.timedSpeechInterval = setInterval(() => {
          this.speechTimeLeft--
          if (this.speechTimeLeft <= 0) {
            this.speechStop()
            clearInterval(this.timedSpeechInterval)
            this.timedSpeechOpen = false
            this.speechTimeLeft == 60
          }
        }, 60000)
      } else {
        clearInterval(this.timedSpeechInterval)
      }
    },

    /** @description 
     * @params defalutDelay 
     */
    autoSpeechTask(
      paraIndex = this.readingPara,
      chapterIndex = this.readingChapter,
      endFlag = false,
      defalutDelay = 500
    ) {
      let readingTime = 50
      const readingText = this.chapterList[this.readingChapter].content[this.readingPara]
      // console.log("utterance.text", this.utterance.text)
      // console.log("readingText", readingText)
      //
      let tempArr = readingText.match(/[$0-9\u4e00-\u9fa5\uf900-\ufa2d]/g)
      if (tempArr && tempArr.length) readingTime += tempArr.length * 320
      //
      tempArr = readingText.match(/[a-zA-Z]/g)
      if (tempArr && tempArr.length) readingTime += tempArr.length * 160
      //
      readingTime = Math.floor(readingTime / this.utterance.rate)
      //
      // let readingTime = Math.floor((readingText.length / this.utterance.rate) * 150)
      // console.log("readingTime", readingTime)

      //(0.6)
      const delay = Math.min(readingTime * 0.6, defalutDelay)

      //()
      if (
        this.readingStatus.paraIndex == this.readingPara &&
        this.readingStatus.chapterIndex == this.readingChapter
      ) {
        this.readingStatus.time += this.isPaused ? 0 : delay
        const maxTime = readingTime * 1.2 + 3000
        // console.log("time", this.readingStatus.time, maxTime)
        if (this.readingStatus.time > maxTime) {
          this.readingStatus = { paraIndex: -1, chapterIndex: -1, time: 0 } //
          this.speechFromPara(this.getParaIndex(this.readingPara))
          return
        }
      } else {
        this.readingStatus = {
          paraIndex: this.readingPara,
          chapterIndex: this.readingChapter,
          time: 0,
        }
      }

      //
      clearTimeout(this.autoSpeechTimer)
      //
      this.autoSpeechTimer = setTimeout(() => {
        this.autoSpeech(paraIndex, chapterIndex, endFlag)
      }, delay)
    },

    /** @description  */
    autoSpeech(paraIndex = this.readingPara, chapterIndex = this.readingChapter, endFlag = false) {
      const debug = false
      if (debug) console.log("autoSpeech readingPara", this.readingPara)

      //
      if (speechSynthesis.pending) {
        if (debug) console.log("")
        this.autoSpeechTask(paraIndex, chapterIndex)
        //
      } else if (speechSynthesis.speaking) {
        if (debug) console.log("")
        //indexindex
        this.readingPara = paraIndex
        this.readingChapter = chapterIndex

        //
        if (this.currentChapter < this.readingChapter) {
          if (debug) console.log("")
          this.currentChapter = this.readingChapter
          //DOM
          this.$nextTick(() => {
            this.readingPage = this.getPageIndex(this.readingPara)
            this.currentPage = this.getPageIndex(this.readingPara)
          }) //
        }
        //
        else if (this.currentChapter == this.readingChapter) {
          if (debug) console.log("")
          this.readingPage = this.getPageIndex(this.readingPara)
          //
          if (this.currentPage < this.readingPage) {
            this.currentPage = this.readingPage //
            if (debug) console.log(" this.currentPage", this.currentPage)
          }
        }

        let chapter = this.chapterList[this.readingChapter].content
        //
        if (this.readingPara + 1 < chapter.length) {
          this.speak(chapter[this.readingPara + 1])
          this.autoSpeechTask(this.readingPara + 1, chapterIndex)
        }
        //
        else if (this.readingChapter + 1 < this.chapterList.length) {
          this.speak(this.chapterList[this.readingChapter + 1].content[0])
          this.autoSpeechTask(0, this.readingChapter + 1, false, 50)
        }
        //
        else {
          this.autoSpeechTask(paraIndex, chapterIndex, true)
        }
      }
      //readingPage
      else {
        if (endFlag) {
          //endFlag
          this.isSpeaking = false
          this.isPaused = false
          return
        }
        if (debug) console.log("")
        this.speak(this.chapterList[chapterIndex].content[paraIndex])
        this.autoSpeechTask(paraIndex, chapterIndex)
      }
    },

    // 20000
    speak(text) {
      //
      // text = text
      //   .replaceAll("", "")
      //   .replaceAll("", "")
      //   .replaceAll("(", "")
      //   .replaceAll(")", "")
      //   .replaceAll("", "")
      //   .replaceAll("--", "")
      //
      if (/[$0-9a-zA-Z\u4e00-\u9fa5\uf900-\ufa2d]/.test(text)) {
        this.utterance.text = text
        speechSynthesis.speak(this.utterance) //
      }
      //
      else {
        const tempUtterance = new SpeechSynthesisUtterance()
        tempUtterance.text = "a" //
        tempUtterance.volume = 0 //0
        speechSynthesis.speak(tempUtterance)
      }
    },

    /** @description  */
    speechFromPara(index) {
      if (!this.utterance) return
      if (speechSynthesis.speaking) {
        this.speechStop() //
        this.readingPara = this.getParaIndexCh(index)
        this.readingPage = this.currentPage
        this.readingChapter = this.currentChapter
        this.isSpeaking = true
        this.isPaused = false
        this.autoSpeech()
      }
    },

    /** @description  */
    speechStart(paraIndex = 0) {
      if (!this.utterance) {
        this.showMessage({ message: "", type: "error", duration: 1000 })
        return
      }
      this.speechStop() //
      this.utterance.text = this.pageList[this.currentPage]
      this.utterance.lang = "zh-CN" //(zh-CNen-US )
      this.utterance.rate = this.speechOptions.rate
      this.utterance.volume = this.speechOptions.volume
      this.utterance.pitch = this.speechOptions.pitch
      this.utterance.voice = this.voiceList[this.voiceIndex]
      //
      this.readingPara = this.getParaIndexCh(paraIndex)
      this.readingPage = this.currentPage
      this.readingChapter = this.currentChapter
      this.isSpeaking = true
      this.isPaused = false
      this.autoSpeech()
    },

    /** @description  */
    getVoiceList() {
      if (!this.utterance) return
      //getVoices(getVoices)
      speechSynthesis.getVoices()
      setTimeout(() => {
        this.voiceList = speechSynthesis
          .getVoices()
          .filter((voice) => voice.lang.indexOf("zh") > -1)
      }, 100)
    },

    /** @description  */
    changeVoice() {
      this.utterance.lang = "zh-CN" //(zh-CNen-US )
      this.utterance.rate = this.speechOptions.rate
      this.utterance.volume = this.speechOptions.volume
      this.utterance.pitch = this.speechOptions.pitch
      this.utterance.voice = this.voiceList[this.voiceIndex]
      this.speechOptions.voiceIndex = this.voiceIndex
      //
      if (speechSynthesis.speaking) {
        this.speechStop() //
        this.isSpeaking = true
        this.isPaused = false
        this.autoSpeech()
      }
    },

    /** @description  */
    speechResume() {
      if (!this.utterance) return
      speechSynthesis.resume()
      this.isPaused = false
    },

    /** @description  */
    speechPause() {
      if (!this.utterance) return
      speechSynthesis.pause()
      this.isPaused = true
    },

    /** @description  */
    speechStop() {
      if (!this.utterance) return
      this.isSpeaking = false
      this.isPaused = false
      clearTimeout(this.autoSpeechTimer) // 
      speechSynthesis.cancel() //
    },

    /** @description  */
    voiceNameFormat(item) {
      return item.name
        .replace("Microsoft", "")
        .replace("Online", "")
        .replace(" (Natural)", "")
        .replace("Chinese", "")
        .replace("Mainland", "")
        .replace("Northeastern Mandarin", "")
        .replace("Zhongyuan Mandarin Shaanxi", "")
        .replace("Hong Kong", "")
        .replace("Cantonese Traditional", "")
        .replace("Taiwanese Mandarin", "")
        .replace("Taiwan", "")
    },

    //#endregion
  },
  watch: {},
}
