    /** @description  */
    repaging(paraList) {
      // var timeStart = performance.now()
      this.pageList = [] //
      this.currentPage = 0 //0
      this.noIndent = [] //

      let debug = false
      if (debug) console.log(">>>>>>>>>>>>> REPAIGING START >>>>>>>>>>>>>") //

      let paras = [] //
      let previosHeight = 0 //
      let currentHeight = 0 //
      let paraLines = 0 //
      let paraIndex = 0 //0
      let paraText = "" //
      let indent = 2 //2

      const lineWidth = this.textareaWidth
      const maxHeight = this.textareaHeight //
      const fontSize = this.sizeSettings.fontSize
      const paraSpace = fontSize * this.sizeSettings.paraSpace //()
      const getFontSize = () => {
        if (paraIndex == 0) return fontSize * this.sizeSettings.titleScale
        else return fontSize
      } //
      const getLineHeight = () => {
        return getFontSize() * this.sizeSettings.lineHeight
      } //

      if (debug) console.log("lineHeight =", getLineHeight(), ", maxHeight =", maxHeight) //

      // START
      while (paraIndex < paraList.length) {
        if (debug) console.log(">>>>>> PARA START >>>>>>") //
        previosHeight = currentHeight //
        //0
        if (paras.length > 0 || this.sizeSettings.paddingTop > 0) currentHeight += paraSpace
        //
        if (paras.length == 1 && currentHeight >= maxHeight) {
          paraText = paras[0]
          paras = [] //
          paraLines = this.getParaLines(paraText, lineWidth, getFontSize(), indent * fontSize) //
          previosHeight = 0
        } else {
          //
          indent = 2 //2
          paraText = paraList[paraIndex]
          paraLines = this.getParaLines(paraText, lineWidth, getFontSize(), indent * fontSize) //
          currentHeight += getLineHeight() * paraLines //
        }
        // if (paraText.indexOf("")) debug = true
        if (debug) console.log("paraText", paraText) //
        if (debug) console.log("fontSize = ", getFontSize()) //
        if (debug) console.log("lineWidth = ", lineWidth) //
        if (debug) console.log("lineHeight = ", getLineHeight()) //
        if (debug) console.log("paraSpace = ", paraSpace) //
        if (debug) console.log("paraLines =", paraLines) //
        if (debug) console.log("previosHeight = ", previosHeight) //
        if (debug) console.log("currentHeight1 = ", currentHeight) //
        if (paraText[0] == undefined) {
          console.log("", this.pageList.length, " ", paras.length + 1, " undefined")
        }
        //
        if (currentHeight < maxHeight) {
          //
          paras.push(paraText)
          //
          if (paraList.length == paraIndex + 1) {
            this.pageList.push(paras) //
          }
        } else {
          //
          const spareLines = Math.floor((maxHeight - previosHeight - paraSpace) / getLineHeight())
          if (debug) console.log("****** spareLines", spareLines) //
          //
          if (spareLines < 1) {
            if (debug) console.log("spareLines < 1") //
            //
            //
            this.pageList.push(paras)
            //
            paras = [paraText]
            //
            if (paraList.length == paraIndex + 1) {
              this.pageList.push(paras) //
            }
            //currentHeight0
            currentHeight =
              paraLines * getLineHeight() + (this.sizeSettings.paddingTop > 0 ? paraSpace : 0)
          } else {
            if (debug) console.log("spareLines >= 1") //
            let sliceIndex = this.getSliceIndex(
              paraText,
              lineWidth,
              getFontSize(),
              indent * fontSize,
              spareLines
            )
            if (debug) console.log("sliceText", paraText.slice(0, sliceIndex)) //
            if (sliceIndex < paraText.length) {
              if (debug) console.log("sliceIndex < paraText.length") //
              //
              paras.push(paraText.slice(0, sliceIndex))
              //
              this.pageList.push(paras)
              //
              this.noIndent.push(this.pageList.length)
              //0
              indent = 0
              //
              paras = [paraText.slice(sliceIndex)]
              //
              paraLines = this.getParaLines(paras[0], lineWidth, getFontSize(), indent * fontSize)
              //currentHeight0
              currentHeight =
                paraLines * getLineHeight() + (this.sizeSettings.paddingTop > 0 ? paraSpace : 0)
              // if(debug) console.log("paraLines", paraLines) //
              //
              if (currentHeight >= maxHeight) {
                paraIndex-- //1
              } else {
                //
                if (paraList.length == paraIndex + 1) this.pageList.push(paras) //
              }
            } else {
              if (debug) console.log("sliceIndex >= paraText.length") //
              //
              paras.push(paraText)
              //
              this.pageList.push(paras)
              //
              paras = []
              //currentHeight0
              currentHeight = 0
            } //
          } //
        } //
        if (debug) console.log("currentHeight2 = ", currentHeight) //
        if (debug) console.log(">>>>>> PARA END >>>>>>") //
        paraIndex++
      }
      // END
      // var timeEnd = performance.now()
      // if (this.device == "desktop") {
      //   console.log("repaging ", `${(timeEnd - timeStart).toFixed(2)}ms`)
      // }
      // else alert("repaging" + `${(timeEnd - timeStart).toFixed(2)}ms`)
    },
    /** @description  */
    getSliceIndex(text, lineWidth, fontSize, indentWidth, limitLines) {
      return this.calcParaLayout(text, lineWidth, fontSize, indentWidth, limitLines)
    },
    /** @description  */
    getParaLines(text, lineWidth, fontSize, indentWidth) {
      return this.calcParaLayout(text, lineWidth, fontSize, indentWidth)
    },
    /** @description  */
    calcParaLayout(text, lineWidth, fontSize, indentWidth, limitLines = 0) {
      const debug = false //text.indexOf("") > -1
      const nonFrontChars = ",.?;:)}]" //
      const nonBackChars = "({[" //
      const nonBreakChars = "" //
      const textLength = text.length
      if (text[0] == undefined) return 0 //
      let width = 0
      let lines = 1
      let index = 0
      let preIndex = 0
      let currentWidth = indentWidth

      /* 2022-11-09 70% */
      /*  */
      //2
      const preload = Math.floor(lineWidth / fontSize) - 4
      index += preload - 2
      //sliceindex1index++index
      currentWidth += this.measureText(text.slice(preIndex, index), fontSize)
      /* 2022-11-09 */

      while (index < textLength) {
        width = this.measureText(text[index], fontSize)
        if (debug) console.log("text[index]", text[index]) //
        if (debug) console.log(currentWidth, lineWidth, width) //
        // <= 
        if (currentWidth <= lineWidth - width) {
          currentWidth += width
        } else {
          //
          let temp = 0 //temp
          //
          while (nonBackChars.indexOf(text[index - 1]) > -1 && index > 1 && temp < 5) {
            if (debug) console.log("nonBackChars text[index-1]", text[index - 1]) //
            index--
            temp++
          }
          temp = 0 //temp
          //
          while (nonFrontChars.indexOf(text[index]) > -1 && index > 0 && temp < 5) {
            if (debug) console.log("nonFrontChars text[index]", text[index]) //
            index--
            temp++
          }
          temp = 0 //temp
          //
          while (
            nonBreakChars.indexOf(text[index]) > -1 &&
            text[index] == text[index - 1] &&
            index > 1 &&
            temp < 5
          ) {
            if (debug) console.log("nonBreakChars text[index]", text[index]) //
            index--
            temp++
          }
          //
          if (debug) console.log("lineText", text.slice(preIndex, index)) //
          if (debug) console.log("currentWidth", currentWidth) //
          preIndex = index
          if (limitLines > 0 && lines >= limitLines) break
          lines++ //+1

          /* 2022-11-09 */
          index += preload
          //sliceindex1index++index
          currentWidth = this.measureText(text.slice(preIndex, index + 1), fontSize)
          /* 2022-11-09 */
        }
        index++
      }
      if (debug) console.log("lines =", lines)
      return limitLines > 0 ? index : lines
    },
    // Canvas
    // getCanvasContext() {
    //   const canvas = document.createElement("canvas")
    //   const context = canvas.getContext("2d")
    //   return context
    // },
    // Canvas
    measureText(val, fontSize) {
      // Canvas
      const fontFamily = this.sizeSettings.fontFamily
      const font = `${fontSize}px ${fontFamily}`
      this.canvasContext.font = font
      const { width } = this.canvasContext.measureText(val)
      return width
      // 
      // if (!val || !val.length) return 0
      // let singleArr = val.match(/[^\x00-\xff]/gi) //eslint-disable-line
      // return val.length + (singleArr ? singleArr.length : 0) * fontSize
    },
